* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(120deg,  #f6d365 0%, #fda085 100%);
  color: white;
  font-family: 'Poppins', sans-serif;
  min-height: 100vh;
}
header {
  font-size: 2rem;
}
header, 
form {
  min-height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
form input, 
form button {
  padding: 0.5rem;
  font-size: 2rem;
border: none;
 background: white;
}

form button {
  color: #ff6f47;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
}
form button:hover {
  color: white;
  background: #ff6f47;
}
.todo-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.todo-list {
  min-width: 30%;
  list-style: none;
}
.todo {
  background: white;
  margin: 0.5rem;
  color: black;
  display: flex;
  font-size: 1.5rem;
  align-items: center;
  justify-content: space-between;
  transition: all 0.5s ease; 
}
.filter-todo {
  padding: 1rem;
}
.todo li {
  flex: 1;
}
.trash-btn, .complete-btn {
  background: #ff6f47;
  color: white;
  border: none;
  padding: 1rem;
  font-size: 1rem;
  cursor: pointer;
}
.complete-btn {
  background: rgb(11, 212, 162);
}
.todo-item {
  padding: 0rem 0.5rem;
}
.fa-trash, .fa-check {
  pointer-events: none;
}
.completed {
  opacity: 0.5;
  text-decoration: line-through;
}
.fall {
  transform: translateY(8rem) rotateZ(20deg);
  opacity: 0;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}
.select {
  position: relative;
  margin: 1rem;
  overflow: hidden;
}
select {
  color: #ff6f47;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  width: 12rem;
}
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background: #ff6f47;
  cursor: pointer;
  pointer-events: none;
}
